.masthead {
	position: relative;
	z-index: 10;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	background-color:$white;
	box-shadow: 0 1px 0 0 rgba($black, 0.1);

	transform: translateY(0%);
	@include transition(0.4s);

	@include lg {
		position: relative;
	}

	&.has-scrolled {
		transform: translateY(-100%);		
		&.scrolling-up {
			transform: none;
		}		
	    @include lg {
	        transform: none;
	    }
	}
}

.site-navigation {
	position:relative;
	width:100%;
	text-align: center;
	ul {
		display: block;
		width:100%;
		padding:0;
		margin:0;
		li {
			position:relative;
			display: inline-block;
			margin:0 -3px;
			text-align: center;
			@include transition(0.3s);
			&:hover, &:active, &:focus,
			&.current_page_item {
			    background-color: $primary;
			    >a {
			    	color:$white;
			    }
			}
			&:last-child ul {
				left:auto;
				right:0;
			}
			a {
				display: block;
				padding:15px;
				@include font-size(1.2,1);
				font-weight: 400;
				color:$gray;
				text-transform: uppercase;
				text-decoration: none !important;
			}
		}
		ul {
		    display: none;
		    position: absolute;
		    z-index: 99;
		    width: 275px;
		    background: $white;
		    left: 0;
		    text-align:left;
		    @include box-shadow(10px, 10px, 20px, rgba(0,0,0,0.15));
		    li {
		    	display: block;
				padding: 0;
		    	margin: 0;
				width: 100%;
				text-align:left;
				a {
					padding:8px 15px;
					color:$primary;
					text-transform: none;
					@include font-size(1);
					letter-spacing: 0;
					text-shadow: none !important;
					&:hover, &:active, &:focus {
					    background-color: $primary;
					    color:$white;
					}
				}
			}
			ul {
				display: none !important;
			}
		}
	}
	ul li:hover>ul {
	    display: block;
	    animation: fadein .25s;
	}
}

@keyframes fadein {
    from {
	    opacity: 0;
	    margin-top: -6px;
	}
	to {
	    opacity: 1;
	    margin-top: -1px;
	}
}