$gutter-padding: 10px;
$gutter-padding-sm: 20px;
$gutter-padding-md: 3vw;
$form-width-md: 30%;

.lightbox-form {
    position: absolute;
    top: 0;
    z-index: 9;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    padding: 0 $gutter-padding;
    width: 100%;
    margin: $gutter-padding auto;
    display: block;
    transition: ease all 0.5s;

    @include md {
        margin: 3vw auto;
        padding: 0 $gutter-padding-sm;
    }

    @include lg {
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        padding: 0 $gutter-padding-md;
        width: 360px;
    }

    @include lg {
        width: $form-width-md;
    }

    &.shown {
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }
}

.lightbox-form-container{

    .fancybox-caption,
    .fancybox-infobar,
    .fancybox-navigation .fancybox-button,
    .fancybox-toolbar {
        position: fixed;

        @include lg {
            position: absolute;
        }
    }

    .fancybox-slide--image {
        padding: 44px 0;

        @include md {
            padding: 44px 0;
        }
    }

   &.fancybox-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-family: inherit;

        @include lg {
            display: block;
        }

        .lightbox-form {
            position: relative;
        }

        .fancybox-inner {
            position: relative;
            left: 0;
            right: initial;
            overflow: visible;
            display: block;
            margin: 0 auto;
            height: 90vw;
            width: 100%;

            @include md {
                height: 60vw;
                width: calc(100% - #{$gutter-padding-sm} * 2);
            }

            @include lg {
                right: $gutter-padding-md;
                margin: 0;
                display: inline-block;
                vertical-align: middle;
                height: 100%;
                width: calc(100% - 360px - #{$gutter-padding-md});
            }

            @include lg {
                width: calc(100% - #{$form-width-md} - #{$gutter-padding-md});
            }
        }

        .fancybox-toolbar div {
            display: inline-block;
            height: 44px;
            line-height: 42px;
            background: rgba(30, 30, 30, 0.6);
            color: #ccc;
            font-size: 12px;
            font-weight: bold;
            min-width: 42px;
            text-align: center;
            white-space: nowrap;
        }
    }
    
    .fancybox-button--arrow_right,
    .fancybox-button--arrow_left{
        text-align: center;
    }
}