html {
    font-size: 16px;
}

body {
    @include primary-font;
    font-size: 1rem;
    font-weight: 400;
    color: $gray;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

p, ol, ul {
    margin-bottom:20px;
    font-size: 1rem;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
    font-weight: 400;
    margin-bottom: 20px;
    line-height: 1;
}

h1, .h1 {
    color: $primary;
    font-size: 1.6rem;
    
    @include md {
        font-size: 1.8rem;
    }
     
    @include lg {
        font-size: 2.4rem;
    }
}
h2, .h2 {
    color: $primary;
    font-size: 1.3rem;
    
    @include md {
        font-size: 1.5rem;
    }    
    @include lg {
        font-size: 1.6rem;
    }
}

h3, .h3 {
    color: $primary;
    font-size: 1.2rem;
    
    @include md {
        font-size: 1.3rem;
    }    
    @include lg {
        font-size: 1.4rem;
    }
}

h4, .h4 {
    color: $primary;
    font-size: 1.1rem;
    
    @include md {
        font-size: 1.2rem;
    }    
    @include lg {
        font-size: 1.3rem;
    }
}

h5, .h5 {
    color: $primary;
    font-size: 1rem;
    
    @include md {
        font-size: 1.1rem;
    }    
    @include lg {
        font-size: 1.2rem;
    }
}

h6, .h6 {
    color: $primary;
    font-size: 1rem;
    
    @include md {
        font-size: 1.1rem;
    }    
    @include lg {
        font-size: 1.2rem;
    }
}


@font-face {
    font-family: 'Font Awesome 5 Pro';
    font-style: normal;
    font-weight: 300;
    font-display: auto;
    src: url('fonts/fontawesome/fa-light-300.eot');
    src: url('fonts/fontawesome/fa-light-300.eot?#iefix') format('embedded-opentype'), 
    url('fonts/fontawesome/fa-light-300.woff2') format('woff2'), url('fonts/fontawesome/fa-light-300.woff') format('woff'), 
    url('fonts/fontawesome/fa-light-300.ttf') format('truetype'), url('fonts/fontawesome/fa-light-300.svg#fontawesome') format('svg'); 
}

.fal {
    font-family: 'Font Awesome 5 Pro';
    font-weight: 300; 
}
  
