@import "mixins";
@import "variables";

html,
body {
    overflow-x: hidden;
}

*:focus {
    outline: 0 !important;
}

a img {
    border:none;
}
a img, 
a:active {
    outline:none;
}

a { 
    color: $secondary; 
}
a:active, a:focus, a:hover { 
    color: $primary;
}
a[href^=tel] {  
    @include md {
        pointer-events:none;
    }
}

.hide {
    display:none !important;
}

#main,
#colophon,
.container {
    margin: 0 auto;
    position:relative;
}
